import * as React from 'react';
import { styled } from '@mui/material/styles';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import ButtonBase from '@mui/material/ButtonBase';
import StarIcon from '@mui/icons-material/Star';
import Stack from '@mui/material/Stack';


const Img = styled('img')({
  margin: 'auto',
  display: 'block',
  maxWidth: '100%',
  maxHeight: '100%',
});

interface WhiskeyProps {
  name: string,
  rating: number,
  price: number,
  cask: string,
  alcohol: number
}

const Whiskey: React.FC<WhiskeyProps> = ({ name, rating, price, cask, alcohol }) => {
  const imageOnLoadHandler = (
    event: React.SyntheticEvent<HTMLImageElement, Event>
  ) => {
    console.log(
      `The image with url of ${event.currentTarget.src} has been loaded`
    );
    if (event.currentTarget.className !== "error") {
      event.currentTarget.className = "success";
      event.currentTarget.onerror = null;
    }
  };

  const imageOnErrorHandler = (
    event: React.SyntheticEvent<HTMLImageElement, Event>
  ) => {
    console.log('error')
    event.currentTarget.src = './image/whisky.png';
    event.currentTarget.className = "error";
    event.currentTarget.onerror = null;
  };
const IMAGE_1 = './image/' + name.replace(/\s/g, '') + '.png';


const loadImg = (src: string): Promise<string> =>
  new Promise((resolve, reject) => {
    const img = new Image();
    img.src = src;
    img.onload = () => resolve(src);
    img.onerror = () => resolve(require('./image/whisky.png'));
  });

  const [src, setSrc] = React.useState("preloadimg");
  React.useEffect(() => {
    const load = async () => {
      await loadImg(
        require('./image/' + name.replace(/\s/g, '').replace(/\(.*$/g, '') + '.png')
      ).then((src) => {
        console.log(src);
        setSrc(src);
      });
    }; // Execute the created function directly
    load();
  }, [src, setSrc]);



  return (
    <Paper
      sx={{
        p: 2,
        margin: 'auto',
        maxWidth: 500,
        flexGrow: 1,
        backgroundColor: (theme) =>
          theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
      }}
    >
      <Grid container spacing={2}>
        <Grid item>
          <ButtonBase sx={{ width: 100, height: 100 }}>
            <Img alt="icon" src={src}/>
          </ButtonBase>
        </Grid>
        <Grid item xs={8} sm container>
          <Grid item xs container direction="column" spacing={2}>
            <Grid item xs>
              <Typography variant="subtitle1" component="div">
                {name}
              </Typography>
              <Stack direction="row" alignItems="center" gap={0} spacing={0.5}>
                <StarIcon sx={{ color: "orange" }} />
                <Typography variant="body1">{rating}</Typography>
              </Stack>
              <Typography variant="body1" color="text.secondary">: {cask}</Typography>
              <Typography variant="body1" color="text.secondary">: {alcohol}%</Typography>
            </Grid>
          </Grid>
          <Grid item>
            <Typography variant="subtitle2" component="div">
              {price.toLocaleString()}
            </Typography>
          </Grid>
        </Grid>
      </Grid>
    </Paper>
  );
}


export default Whiskey;
